  position: relative;
  z-index: 2;
}

.brk-tiles-full__content:before {
  content: '';
  position: absolute;
  top: 50%;
  left: -16px;
  transform: translateY(-50%);
  width: 0;
  height: 0;
  transition: all .4s ease-in-out;
